"use client"

import { Button } from "@/components/ui/button"
import { Instagram, MessageCircle } from "lucide-react"
import { useRef, useState, useEffect } from "react"

interface ProductSpecsProps {
  onAdoptClick?: () => void
}

export function ProductSpecs({ onAdoptClick }: ProductSpecsProps) {
  const imageRef = useRef<HTMLDivElement>(null)
  const [transform, setTransform] = useState({ rotateX: 0, rotateY: 0, scale: 1 })
  const [isMobile, setIsMobile] = useState(false)

  // Detect mobile device
  useEffect(() => {
    const checkMobile = () => {
      const mobile = window.innerWidth <= 768 || "ontouchstart" in window
      setIsMobile(mobile)
    }
    checkMobile()
    window.addEventListener("resize", checkMobile)
    return () => window.removeEventListener("resize", checkMobile)
  }, [])

  // Mouse effect for desktop - multi-layer parallax
  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    if (isMobile) return // Don't use mouse effect on mobile

    if (!imageRef.current) return

    const rect = imageRef.current.getBoundingClientRect()
    const x = e.clientX - rect.left
    const y = e.clientY - rect.top

    const centerX = rect.width / 2
    const centerY = rect.height / 2

    const rotateX = ((y - centerY) / centerY) * -15
    const rotateY = ((x - centerX) / centerX) * 15

    setTransform({ rotateX, rotateY, scale: 1.05 })
  }

  const handleMouseLeave = () => {
    if (isMobile) return // Don't reset on mobile
    setTransform({ rotateX: 0, rotateY: 0, scale: 1 })
  }

  // Layer configurations for parallax effect
  const layers = [
    { src: "/images/01.png", depth: 0.3, animation: "float-layer-1" },
    { src: "/images/02.png", depth: 0.5, animation: "float-layer-2" },
    { src: "/images/03.png", depth: 0.7, animation: "float-layer-3" },
    { src: "/images/04.png", depth: 0.9, animation: "float-layer-4" },
  ]

  const specs = [
    { label: "Material", value: "Plástico" },
    { label: "Dimensiones", value: "80x80mm" },
    { label: "Color", value: "Turquesa, rosa y blanco" },
    { label: "Sistema", value: "Imantado con neodimio y cierre hermético" },
    { label: "Packaging", value: "Caja de colección con diseño exclusivo" },
    { label: "Fabricación", value: "Impresión 3D" },
  ]

  return (
    <section className="py-24 bg-black">
      <div className="w-full max-w-7xl mx-auto px-4">
        <div className="max-w-7xl mx-auto space-y-8">
          <div className="text-center space-y-2 max-w-2xl mx-auto">
            <h2 className="text-4xl sm:text-5xl lg:text-6xl font-black text-white">Especificaciones</h2>
            <p className="text-lg sm:text-xl text-gray-400">
              Diseñado y fabricado con los más altos estándares de calidad.
            </p>
          </div>

          <div className="grid lg:grid-cols-2 gap-12 items-center">
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              {specs.map((spec, index) => (
                <div
                  key={index}
                  className="bg-gradient-to-br from-zinc-900/80 to-zinc-900/40 border border-cyan-500/30 rounded-lg p-6 space-y-2 hover:border-cyan-400 hover:shadow-lg hover:shadow-cyan-500/20 transition-all duration-300"
                >
                  <p className="text-xs text-cyan-400 font-bold uppercase tracking-wider">{spec.label}</p>
                  <p className="text-white font-medium text-base">{spec.value}</p>
                </div>
              ))}
            </div>

            <div className="relative flex justify-center lg:justify-end items-center perspective-1000">
              <div
                ref={imageRef}
                onMouseMove={handleMouseMove}
                onMouseLeave={handleMouseLeave}
                className="cursor-pointer relative w-full max-w-[280px] sm:max-w-sm md:max-w-md"
                style={{
                  height: "500px",
                  transformStyle: "preserve-3d",
                }}
              >
                {layers.map((layer, index) => (
                  <div
                    key={index}
                    className={`absolute inset-0 w-full h-full ${layer.animation}`}
                  >
                    <img
                      src={layer.src}
                      alt={`Pieza ${index + 1} del despiece TeddyTwist`}
                      className="absolute inset-0 w-full h-full object-contain select-none"
                      draggable="false"
                      loading="lazy"
                      style={
                        isMobile
                          ? {
                              filter: `drop-shadow(0 20px 30px rgba(6, 182, 212, ${0.3 - index * 0.05}))`,
                            }
                          : {
                              transform: `perspective(1000px) rotateX(${transform.rotateX * layer.depth}deg) rotateY(${transform.rotateY * layer.depth}deg) scale(${1 + (transform.scale - 1) * layer.depth}) translateZ(${index * 20}px)`,
                              transition: "transform 0.2s ease-out",
                              filter: `drop-shadow(0 ${transform.scale === 1.05 ? 30 + index * 5 : 20 + index * 3}px ${transform.scale === 1.05 ? 40 + index * 5 : 30 + index * 3}px rgba(6, 182, 212, ${transform.scale === 1.05 ? 0.4 - index * 0.05 : 0.2 - index * 0.03}))`,
                            }
                      }
                    />
                  </div>
                ))}
              </div>
            </div>
          </div>

          <div className="bg-gradient-to-br from-cyan-900/30 via-purple-900/20 to-pink-900/30 border border-cyan-500/20 rounded-2xl p-8 sm:p-12 lg:p-16 text-center space-y-8">
            <div className="space-y-4">
              <h3 className="text-3xl sm:text-4xl lg:text-5xl font-black text-white">Solo 900 Unidades</h3>
              <p className="text-base sm:text-lg text-gray-300 max-w-2xl mx-auto leading-relaxed">
                Esta es tu oportunidad de poseer una pieza única de arte coleccionable. Una vez vendidas, nunca volverán
                a estar disponibles.
              </p>
            </div>

            <div className="flex flex-col items-center gap-4 sm:gap-6 pt-4">
              <Button
                size="lg"
                className="text-base sm:text-lg font-bold h-12 sm:h-14 bg-cyan-400 hover:bg-cyan-500 text-black w-full sm:w-auto sm:min-w-72 rounded-md"
                onClick={onAdoptClick}
              >
                ADOPTAR A BUZZY
              </Button>

              <p className="text-xs sm:text-sm md:text-base text-gray-300 text-center px-4">
                Envío gratuito dentro de Córdoba capital • Garantía de un mes
              </p>

              <div className="flex flex-col sm:flex-row items-center justify-center gap-3 sm:gap-4 pt-2 w-full sm:w-auto">
                <Button
                  variant="outline"
                  size="lg"
                  className="gap-2 bg-transparent border-none hover:bg-white/10 w-full sm:w-auto text-sm sm:text-base"
                  asChild
                >
                  <a href="https://www.instagram.com/teddytwist_/" target="_blank" rel="noopener noreferrer">
                    <Instagram className="w-4 h-4 sm:w-5 sm:h-5" />
                    Síguenos en Instagram
                  </a>
                </Button>

                <Button
                  variant="outline"
                  size="lg"
                  className="gap-2 bg-transparent border-none hover:bg-white/10 w-full sm:w-auto text-sm sm:text-base"
                  asChild
                >
                  <a
                    href="https://wa.me/5493516353296?text=Hola%2C%20me%20gustaria%20adoptar%20un%20osito%20de%20peluche%F0%9F%A7%B8"
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <MessageCircle className="w-4 h-4 sm:w-5 sm:h-5" />
                    Consultas
                  </a>
                </Button>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* YouTube Video Section */}
      <div className="mt-12 sm:mt-24 w-full max-w-7xl mx-auto px-4">
        <div className="max-w-2xl mx-auto">
          <div className="bg-gradient-to-br from-zinc-900/80 to-zinc-900/40 border border-cyan-500/30 rounded-2xl p-4 sm:p-8 md:p-12 space-y-4 sm:space-y-6">
            <div className="text-center space-y-2 sm:space-y-3">
              <h3 className="text-2xl sm:text-3xl md:text-4xl font-black text-white">Ve a BUZZY en Acción</h3>
              <p className="text-sm sm:text-base md:text-lg text-gray-300">Descubre cómo funciona este increíble picador</p>
            </div>
            <div className="relative w-full max-w-sm mx-auto" style={{ paddingBottom: "177.78%", maxHeight: "70vh" }}>
              <iframe
                className="absolute top-0 left-0 w-full h-full rounded-lg"
                src="https://www.youtube.com/embed/cYwepEKb2pE"
                title="BUZZY × TEDDYTWIST - Video Demostración"
                frameBorder="0"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
                loading="lazy"
              />
            </div>
          </div>
        </div>
      </div>

      <footer className="mt-12 sm:mt-24 border-t border-zinc-800 py-8 sm:py-12">
        <div className="w-full max-w-7xl mx-auto px-4">
          <div className="max-w-4xl mx-auto text-center space-y-3 sm:space-y-4">
            <div className="flex items-center justify-center gap-2 sm:gap-3">
              <img src="/images/logo-alpha.webp" alt="TeddyTwist Logo" className="w-10 h-10 sm:w-12 sm:h-12 object-contain" loading="lazy" />
              <span className="text-xl sm:text-2xl font-black text-white">TEDDYTWIST</span>
            </div>
            <p className="text-xs sm:text-sm text-gray-400">© 2025 TeddyTwist. Todos los derechos reservados.</p>
          </div>
        </div>
      </footer>
    </section>
  )
}
